import {Component} from '@angular/core';
import {IonicPage, LoadingController, NavController, NavParams} from 'ionic-angular';
import {BranchProvider} from "../../../providers/branch/branch-provider";
import {BranchModel} from "../../../models/branch-model";

@IonicPage()
@Component({
  selector: 'page-chat',
  templateUrl: 'branch.html',
})
export class Branch{

  branches: BranchModel[] ;
  private uid : any
    private  phone: any


  constructor(navParams: NavParams,
              public navCtrl: NavController,
              private branch: BranchProvider,
              private loading: LoadingController,

             ) {
     this.uid = navParams.get('uid');
      this.phone = navParams.get('phone')

  }

  ionViewWillLeave() {

  }

  ionViewDidEnter() {
    //get notice list
    this.getBranches();

    // Subscribe to received  new notice events

  }



  getBranches(){
    const loader = this.loading.create({
      content: "Please wait...",
    });
    loader.present()


    this.branch.query().then((res: any) => {
      this.branches = JSON.parse(res.data);
      loader.dismissAll()
    });
    }



openCell(obj){
  this.navCtrl.push('SelectCellgroups', {

    data: obj,
      phone:this.phone,
      uid:this.uid
  });

}





}



